$primary: rgb(190, 32, 46);
$secondary: rgb(179, 175, 172);
$success: rgba(75, 175, 7, 1);
$background: rgba(250, 251, 252, 1);
$info: rgb(225, 233, 214);
$warning: rgba(201, 169, 56, 1);
$danger: rgba(201, 73, 73, 1);
$light: rgb(230, 230, 230);
$dark: rgba(1, 1, 1, 1);

@mixin transition($speed) {
  -webkit-transition: all $speed ease-in-out;
  -moz-transition: all $speed ease-in-out;
  -ms-transition: all $speed ease-in-out;
  -o-transition: all $speed ease-in-out;
  transition: all $speed ease-in-out;
}

#makesf {
  .makesf-text-field {
    width: 100%;
  }
}

.woocommerce {
  .progress-container {
    .progress-item {
      a {
        text-decoration: none;
      }
      &.bg-success {
        &:hover {
          background-color: darken($success, 15%) !important;
        }
      }
      &.bg-danger {
        &:hover {
          background-color: darken($danger, 15%) !important;
        }
      }
    }
  }
}

#memberResources {
  .member-badge {
    border: 1px dashed black;
    .badge-header {
      background: $primary;
      text-align: center;
      padding: 15px;
      img {
        width: 33%;
        margin: 0 auto;
        height: auto;
      }
    }
    .badge-body {
      text-align: center;
      h2 {
        font-size: 1.7em;
      }
      h3 {
        font-size: 1.2em;
        font-style: italic;
      }
      .qr-code {
        padding: 30px;
      }
    }
  }
}

#MAKEMemberSignIn {
  width: 100%;
  margin: 0 auto;
  overflow-x: hidden;

  #result {
    position: relative;
    // height: 60%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding-bottom: 96px;

    .loading {
      position: absolute;
      font-size: 3em;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $primary;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.5);
    }

    .no-user {
    }

    .email-form {
      width: 500px;
      form {
      }
    }

    button:disabled {
      opacity: 0.25;
    }

    /* Layout and spacing */
    .badge-list {
      margin-bottom: 0;
    }

    .badge-footer {
      position: fixed;
      width: 100%;
      background: white;
      bottom: 0;
      padding: 16px 12px;
      box-shadow: 0 0 10px 0 rgba(1, 1, 1, 0.1);
    }
  }

  // #reader__dashboard {
  //   display: none;
  // }

  .profile-container {
    display: flex;
    cursor: pointer;
    margin-top: 40px;

    .profile-card {
      width: 100%;
      max-width: 400px;
      margin: 0 auto;
      border-radius: 10px;
      overflow: hidden;
      border: 1px solid #efefef;
      background: white;
      overflow: visible;
      text-align: center;
      border-radius: 10px;
      padding: 15px;
      -webkit-box-shadow: 0 0 10px 0 rgba(1, 1, 1, 0.2);
      box-shadow: 0 0 10px 0 rgba(1, 1, 1, 0.2);

      img.profile-image {
        margin-top: -75px;
        width: 150px;
        height: 150px;
        border-radius: 75px;
        border: 2px solid #eeeeee;

        // Green glow for signed-in volunteers
        &.volunteer-signed-in {
          border: 3px solid #28a745;
          box-shadow: 0 0 15px rgba(40, 167, 69, 0.4);
        }
      }

      .profile-info {
        h3 {
          //name
          margin: 10px 0 0 0;
          font-size: 24px;
        }

        span.membership {
          display: block;
          font-style: italic;
          font-size: 14px;
          color: #4f4f4f;
        }
      }

      &.selected {
        border: 3px solid $primary;
      }
    }
  }

  .badge-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 12px;

    /* Shared card styles */
    .badge-item,
    .activity-item {
      border: 1px solid #efefef;
      border-radius: 10px;
      background: #fff;
      padding: 12px;
      box-shadow: 0 0 10px 0 rgba(1, 1, 1, 0.1);
      transition: box-shadow 120ms ease, border-color 120ms ease;

      &:hover {
        box-shadow: 0 0 10px 0 rgba(1, 1, 1, 0.2);
        border-color: #dcdcdc;
      }

      &.selected {
        border: 3px solid $primary;
        box-shadow: 0 0 0 3px rgba(190, 32, 46, 0.08) inset;
      }
    }

    //expiration timer 
    .badge-expiration {
      margin-top: 8px;
      font-size: 0.85em;
      color: $success;
      font-weight: bold;
    }


    /* Square badge cards (non-activity) */
    .badge-item:not(.activity-item) {
      width: 200px;
      aspect-ratio: 1 / 1;
      margin: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      overflow: hidden;

      &.not-allowed {
        opacity: 0.4;
      }

      img {
        max-width: 76%;
        max-height: 60%;
        height: auto;
        object-fit: contain;
        margin-top: 4px;
      }

      span {
        white-space: normal;
        word-break: break-word;
        text-align: center;
        margin-top: auto;
        padding: 6px;
        line-height: 1.15;
      }
    }

    /* Full-width activity items */
    .activity-item {
      width: 100% !important;
      max-width: none;

      h3 {
        margin: 8px 0;
      }
    }
  }

  .badge-header {
    width: 100%;
  }

  .sign-in-footer {
    button {
      &.removed {
        opacity: 0;
      }
    }
  }
}

//Blocks

.make-badge-list {
  .badge-card {
    @include transition(500ms);
    -webkit-box-shadow: 0 0 10px 0 rgba(1, 1, 1, 0.1);
    box-shadow: 0 0 10px 0 rgba(1, 1, 1, 0.1);
    a {
      text-decoration: none;
    }
    &:hover {
      @include transition(100ms);
      -webkit-box-shadow: 0 0 10px 0 rgba(1, 1, 1, 0.4);
      box-shadow: 0 0 10px 0 rgba(1, 1, 1, 0.4);
    }
  }
}

.make-image-slider {
  overflow-x: hidden;
  .gallery-images {
    position: relative;
    .gallery-controles {
      position: absolute;
      top: 46%;
      width: 80px;
      left: -30px;
      z-index: 1000;
      display: flex;
      justify-content: space-between;
      span {
        display: block;
        width: 40px;
        padding: 5px 0 5px 0;
        display: flex;
        justify-content: center;
        flex-direction: row;
        align-items: center;
        text-align: center;
        font-size: 1.3em;
        border-width: 1px;
        border-style: solid;
        border-color: #707070;
        background: #fff;
        @include transition(200ms);
        &.prev {
          border-right: none;
          border-radius: 8px 0 0 8px;
        }
        &.next {
          border-left: none;
          border-radius: 0px 8px 8px 0;
        }
        &:hover {
          background-color: #efefef;
          cursor: pointer;
        }
        &.slick-disabled {
          opacity: 0;
        }
      }
    }
  }
  .gallery-slideshow {
    .gallery-slide {
      padding: 0 10px;
      .caption {
        padding: 0 10px;
        color: #4a4a4a;
        font-size: 0.8em;
      }
    }
  }
}
